*{
    margin: 0px;
    padding: 0px;
}

body{
    font-family: 'Rubik', sans-serif;
    font-size: 14px;
    font-weight: 400;
}

.App{
    max-width: 1440px;
    width: 100%;
    height: 100%;
    margin: auto auto;
    color: hsl(229, 8%, 60%);
    background-color: #FFF;
    background-image: url(../src/images/bg-simplify-section-desktop.svg);
    background-repeat: no-repeat;
}

li{
    list-style-type: none;
}